<template>
	<div class="home-wrapper" ref="scrollWrapper">
		<div class="home" ref="home">
			<!-- 异步加载轮播图的情况 -->
			<Swiper v-if="list.length > 0" duration="300">
			   <Slide v-for="(item,index) in list" :key="index">
			   		<img :src="item.imgUrl" width="100%">
			   </Slide>	
			</Swiper>
			
			<!-- 快捷导航菜单 -->
			<Nav></Nav>
			
			<!-- 公告新闻 -->
			<Announcement></Announcement>
			
			<!-- 活动广告 -->
			<div class="banner">
				<img src="../../assets/image/ban_1.png">
			</div>
			
			<!-- 秒杀 -->
			<Seckill></Seckill>

			<!-- 店铺区 -->
			<store-area></store-area>

			<!-- 产品分类区 -->
			<products-area></products-area>	
		</div>

		<router-view></router-view>
	</div>
</template>

<script>

/*
	自动轮播插件 https://github.com/zwhGithub/vue-swiper
	autoPlay 	是否自动轮播 	true
	showIndicator 	是否显示轮播的那个点 	true
	interval 	每两次隔多久滚动一次 	2500
	duration 	每次滚动一页需要多久时间 	500
*/
import { Swiper, Slide } from 'vue-swiper-component'
import vNav from './Nav/index'
import Announcement from './Announcement/index'
import Seckill from './Seckill/index'
import StoreArea from './StoreArea/index'
import ProductsArea from './ProductsArea/index'

// 惯性滑动
import BScroll from 'better-scroll'

export default {
	components: {
		Swiper,
		Slide,
		vNav,
		Announcement,
		Seckill,
		StoreArea,
		ProductsArea
	},
	data() {
		return {
			list: [
				{
					imgUrl: 'https://pic.ule.com/item/user_0102/desc20180413/1f1808b369432ebe_-1x-1.jpg'
				},
				{
					imgUrl: 'https://pic.ule.com/item/user_0102/desc20180330/5e144c19cb7d4433_-1x-1.jpg'
				},
				{
					imgUrl: 'https://pic.ule.com/item/user_0102/desc20180404/8e86b6f54645b7f9_-1x-1.jpg'
				}
			]
		}
	},

	methods: {
		_initScroll() {
	      this.scroll = new BScroll(this.$refs.scrollWrapper, {
	        // click: true,
	        scrollY: true,
	        probeType: 1
	      });
	    },
	},

	mounted() {
		this._initScroll();
	}
}
</script>

<style lang="less" scoped>
	.home-wrapper {
		height: 100%;
		overflow: hidden;
		.home {
			padding-bottom: .5rem;
			.banner {
				width: 100%;
				height: .72rem;
				img {
					width: 100%;
					height: 100%;
				}
			}
		}
	}
</style>


